.main-navigation {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-width: 320px;
  background-color: #81b3d2;
  /*min-width: 320px;
  margin: 0 auto;*/ }

.main-navigation__list {
  margin: 0;
  padding: 0; }

.main-navigation__toggle {
  position: absolute;
  top: 16px;
  right: 20px;
  z-index: 2;
  display: block;
  width: 23px;
  height: 25px;
  background-color: transparent;
  cursor: pointer;
  border: none; }

.main-navigation--closed .main-navigation__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 8px 0 0 #81b3d2, 0 16px 0 0 #81b3d2; }

.main-navigation--opened .main-navigation__toggle {
  top: 15px;
  right: 21px; }
  .main-navigation--opened .main-navigation__toggle::before, .main-navigation--opened .main-navigation__toggle::after {
    content: "";
    position: absolute;
    top: 11px;
    left: 0;
    width: 26px;
    height: 4px;
    background-color: #ffffff; }
  .main-navigation--opened .main-navigation__toggle::before {
    transform: rotate(45deg);
    box-shadow: none; }
  .main-navigation--opened .main-navigation__toggle::after {
    transform: rotate(-45deg); }
  .main-navigation--opened .main-navigation__toggle:active::before, .main-navigation--opened .main-navigation__toggle:active::after {
    background-color: rgba(255, 255, 255, 0.3); }

@media (min-width: 768px) {
  .main-navigation {
    flex-direction: row;
    min-width: 768px;
    margin: 0 auto; }
  .main-navigation__toggle {
    display: none; }
  .main-navigation--closed .main-navigation__toggle::before {
    display: none; } }

@media (min-width: 1200px) {
  .main-navigation {
    width: 1200px; } }

@media (max-width: 767px) {
  .main-navigation--closed .site-list__item {
    display: none; }
  .main-navigation--no-js .main-navigation__toggle {
    display: none; }
  .main-navigation--no-js .site-list__item {
    display: block; } }

